export var START_NEW_WEEK = "START_NEW_WEEK";
export var startNewWeek = function startNewWeek() {
  return {
    type: START_NEW_WEEK
  };
};
export var SAVE_HABIT = "SAVE_HABIT";
export var saveHabit = function saveHabit(id, name, type, frequency) {
  return {
    type: SAVE_HABIT,
    payload: {
      id: id,
      name: name,
      type: type,
      frequency: frequency
    }
  };
};
export var DELETE_HABIT = "DELETE_HABIT";
export var deleteHabit = function deleteHabit(id) {
  return {
    type: DELETE_HABIT,
    payload: {
      id: id
    }
  };
};
export var TOGGLE_DAY_HABIT = "TOGGLE_DAY_HABIT";
export var toggleDayHabit = function toggleDayHabit(week, name, day) {
  return {
    type: TOGGLE_DAY_HABIT,
    payload: {
      week: week,
      name: name,
      day: day
    }
  };
};