import { createStore, combineReducers } from "redux";
import { persistStore, persistReducer, createMigrate } from "redux-persist";
import storage from "redux-persist/lib/storage";
import habitsReducer from "./reducers/habitsReducer";
import userReducer from "./reducers/userReducer";
import migrations from "./migrations";
var rooReducer = combineReducers({
  habits: habitsReducer,
  user: userReducer
});
var persistConfig = {
  key: "root",
  storage: storage,
  version: 0,
  migrate: createMigrate(migrations)
};
var persistedReducer = persistReducer(persistConfig, rooReducer);
var store = createStore(persistedReducer, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__());
var persistor = persistStore(store);
export { store, persistor };